.main-nav__toggle {
  position: absolute;
  top: 22px;
  right: 20px;
  width: 24px;
  height: 18px;
  z-index: 2;
}
.main-nav__toggle::before, .main-nav__toggle::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #000000;
}
.main-nav__toggle::before {
  top: 0;
}
.main-nav__toggle::after {
  bottom: 0;
}
.main-nav__toggle:active {
  opacity: 0.5;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle-icon {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  height: 2px;
  background-color: #000000;
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  top: 8px;
}

.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
  bottom: 8px;
}

.main-nav--opened .main-nav__list {
  position: absolute;
  top: 65px;
  left: 0;
  z-index: 10;
  width: 100%;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .main-nav--opened .main-nav__list {
    position: static;
    background-color: inherit;
  }
}

.main-nav--opened .main-nav__link {
  width: 100%;
  margin: 0;
}

.main-nav--opened .main-nav__toggle-icon {
  display: none;
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__toggle::before {
  content: none;
}
.main-nav--nojs .main-nav__toggle::after {
  content: none;
}

@media (max-width: 767px) {
  .main-nav--nojs .main-nav__list {
    position: static;
    display: block;
    margin: 0 -20px;
    overflow: hidden;
  }
}

